// -----------------------------------------------------------------------------
//! RESET
// -----------------------------------------------------------------------------

html,
body,
header,
footer,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
ul,
ol,
li,
img,
fieldset,
input,
textarea,
select {
    margin: 0;
    padding: 0;
}

html,
body {
    min-height: 100%;
}

body {
    -webkit-text-size-adjust: none;
}

main,
article,
section,
aside {
    display: block;
}

:focus {
    outline: none;
}

// Accessibility
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

.sr-only-focusable {
    &:active,
    &:focus {
        position: static;
        width: auto;
        height: auto;
        margin: 0;
        overflow: visible;
        clip: auto;
    }
}

// Print
@media screen {
    .print-only {
        display: none !important;
    }
}

@media print {
    .no-print {
        display: none !important;
    }
}

// Ditch the borders
a img,
button,
input,
textarea {
    border: 0;
}

// Images and Embeds
img,
object,
embed {
    max-width: 100%;
    height: auto;
}
img {
    -ms-interpolation-mode: bicubic;
}
#map_canvas img,
.map_canvas img {
    max-width: none !important;
}
iframe {
    border: 0;
}

// Border-box all of the things
* {
    box-sizing: border-box;

    :before,
    :after {
        box-sizing: inherit;
    }
}

// Hide alt text
.alt {
    display: none;
}
