@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
html {
  height: 100%;
  background: #fff;
}

body {
  position: relative;
  min-height: 100%;
  margin: 0;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 1240px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

section {
  margin-top: 45px;
}
@media only screen and (min-width: 900px) {
  section {
    margin-top: 95px;
  }
}
section:first-of-type {
  margin-top: 0;
}
section:last-of-type {
  margin-bottom: 24px;
}
@media only screen and (min-width: 768px) {
  section:last-of-type {
    margin-bottom: 55px;
  }
}
section.no-margin-top {
  margin-top: 0;
}

section.hero .container {
  margin-top: 0;
}

html,
body,
header,
footer,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
ul,
ol,
li,
img,
fieldset,
input,
textarea,
select {
  margin: 0;
  padding: 0;
}

html,
body {
  min-height: 100%;
}

body {
  -webkit-text-size-adjust: none;
}

main,
article,
section,
aside {
  display: block;
}

:focus {
  outline: none;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

@media screen {
  .print-only {
    display: none !important;
  }
}
@media print {
  .no-print {
    display: none !important;
  }
}
a img,
button,
input,
textarea {
  border: 0;
}

img,
object,
embed {
  max-width: 100%;
  height: auto;
}

img {
  -ms-interpolation-mode: bicubic;
}

#map_canvas img,
.map_canvas img {
  max-width: none !important;
}

iframe {
  border: 0;
}

* {
  box-sizing: border-box;
}
* :before,
* :after {
  box-sizing: inherit;
}

.alt {
  display: none;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: "Poppins", sans-serif;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-variant-ligatures: common-ligatures;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", sans-serif;
  line-height: 1.2;
}

p {
  line-height: 1.5;
  font-size: 1em;
  font-family: "Poppins", sans-serif;
  font-variant-numeric: lining-nums;
  font-feature-settings: "lnum";
}
p:first-child {
  margin-top: 0;
}
p a {
  color: #2ebcb3;
  border-bottom: 2px solid transparent;
  transition: border-color 0.2s ease;
}
p a:hover {
  border-color: #2ebcb3;
  text-decoration: none;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

ul,
ol {
  margin: 0;
  padding: 0;
}
ul li,
ol li {
  padding: 0;
  margin: 0;
}

.section-title,
h2 {
  font-size: 3rem;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
  margin-bottom: 22px;
}
@media only screen and (min-width: 768px) {
  .section-title,
  h2 {
    font-size: 4rem;
  }
}

.section-title.center,
.section-subtitle.center {
  text-align: center;
}

.section-text,
.section-subtitle {
  font-size: 1.6rem;
}
@media only screen and (min-width: 768px) {
  .section-text,
  .section-subtitle {
    font-size: 1.8rem;
  }
}
.section-text a,
.section-subtitle a {
  border-bottom: 1px solid transparent;
}
.section-text a:hover,
.section-subtitle a:hover {
  text-decoration: none;
}

.error404 .page-section .container {
  max-width: 900px;
  text-align: center;
}
.error404 .btn {
  margin-top: 32px;
}

.accordion-container {
  max-width: 850px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 20px;
}

.accordion {
  width: 100%;
  cursor: pointer;
  border-bottom: 2px solid rgba(125, 125, 125, 0.2);
  padding-bottom: 24px;
  transition: border-color 0.2s ease;
  margin-bottom: 32px;
}
.accordion:last-of-type {
  margin-bottom: 0;
}
.accordion:hover {
  border-color: #2ebcb3;
}
.accordion:hover svg path {
  stroke: #2ebcb3;
}
.accordion svg {
  transition: transform 0.2s ease, fill 0.2s ease;
  width: 18px;
  height: 16px;
  flex: 0 0 18px;
}
@media only screen and (min-width: 768px) {
  .accordion svg {
    width: 24px;
    height: 22px;
    flex: 0 0 24px;
  }
}
.accordion svg path {
  transition: stroke 0.2s ease;
  fill: #333;
}
.accordion.is-active svg {
  transform: rotate(-180deg);
}

.accordion-title {
  width: 100%;
  line-height: 1.5;
  font-size: 1.6rem;
  transition: color 0.2s ease;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  font-weight: 500;
}
@media only screen and (min-width: 768px) {
  .accordion-title {
    font-size: 2rem;
  }
}

.accordion-content {
  display: none;
  overflow: hidden;
  font-size: 1.6rem;
  margin-top: 16px;
}

.local-info {
  padding: 20px 0 0 0;
}
.local-info .accordion {
  border: none;
}
.local-info .accordion-title {
  font-size: 1.2rem;
  width: auto;
  display: inline-block;
  color: #eaeaea;
}
.local-info .accordion-content {
  color: #eaeaea;
}
.local-info:hover .accordion-title {
  color: #eaeaea;
}

.articles-top {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 48px;
}
@media only screen and (min-width: 768px) {
  .articles-top {
    flex-direction: row;
    align-items: center;
  }
}

.articles-text {
  flex: 1;
}

.articles-toggle {
  flex: 1;
  display: inline-flex;
  flex-direction: column;
  align-items: start;
  gap: 16px;
}
@media only screen and (min-width: 768px) {
  .articles-toggle {
    justify-content: flex-end;
    flex-direction: row;
    gap: 24px;
  }
}

button.toggle {
  background: none;
  padding: 0;
  padding-bottom: 4px;
  cursor: pointer;
  white-space: nowrap;
  font-size: 1.6rem;
  font-weight: 600;
  border-bottom: 2px solid transparent;
}
button.toggle.active {
  border-color: #2ebcb3;
}
@media only screen and (min-width: 768px) {
  button.toggle {
    font-size: 2rem;
  }
}

.articles-container {
  display: none;
  grid-template-columns: 1fr;
  margin-top: 32px;
  gap: 32px;
}
@media only screen and (min-width: 768px) {
  .articles-container {
    grid-template-columns: 1fr 1fr;
    margin-top: 64px;
  }
}
@media only screen and (min-width: 900px) {
  .articles-container {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.articles-container.active {
  display: grid;
}

.article {
  background-color: #f7f4f2;
  padding: 25px;
  color: #333;
  display: flex;
  flex-direction: column;
}
.article:hover {
  text-decoration: none;
}
.article:hover .article-title span {
  border-color: #2ebcb3;
}

.date {
  margin-bottom: 8px;
}

.article-title {
  display: inline-block;
  width: auto;
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 16px;
}
.article-title span {
  border-bottom: 2px solid transparent;
  transition: border-color 0.2s ease;
}

.description {
  font-size: 1.4rem;
  margin-bottom: 12px;
}

.publisher {
  font-size: 1.2rem;
  font-style: italic;
  margin-top: auto;
}

.btn {
  color: #fff;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
  font-size: 1.4rem;
  padding: 14px 44px;
  background-color: #bababa;
  transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease;
  text-decoration: none;
  display: inline-block;
  border-radius: 100px;
  white-space: nowrap;
  cursor: pointer;
}
@media only screen and (min-width: 768px) {
  .btn {
    padding: 16px 55px;
    font-size: 1.6rem;
  }
}
.btn:hover {
  text-decoration: none;
}

.btn-green {
  background-color: #2ebcb3;
}
.btn-green:hover {
  background-color: #24938c;
}

.btn-black {
  background-color: #333;
}
.btn-black:hover {
  background-color: #4d4d4d;
}

footer {
  padding-top: 22px;
  padding-bottom: 22px;
  margin-top: auto;
}
@media only screen and (min-width: 768px) {
  footer {
    padding-top: 45px;
    padding-bottom: 45px;
  }
}

.links a {
  transition: border 0.2s ease-in-out;
  border-bottom: 1px solid transparent;
}
.links a:hover {
  text-decoration: none;
}

.footer-top {
  margin-bottom: 46px;
}

.footer-top {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 50px;
}
@media only screen and (min-width: 768px) {
  .footer-top {
    flex-direction: row;
  }
}

.footer-bottom {
  text-align: center;
}

.footer-info-container {
  display: flex;
  flex-wrap: wrap;
  gap: 42px;
}
@media only screen and (min-width: 768px) {
  .footer-info-container {
    align-items: center;
  }
}
.footer-info-container a {
  color: #333;
}

.footer-logo {
  max-width: 150px;
}
.footer-logo svg {
  width: 100%;
  height: auto;
}

.hero {
  position: relative;
  z-index: 1;
  background-color: #f7f4f2;
}

.hero-content {
  padding-top: 170px;
  padding-bottom: 30px;
}
@media only screen and (min-width: 768px) {
  .hero-content {
    padding-bottom: 50px;
    padding-top: 170px;
    max-width: 800px;
  }
}

.hero-title {
  font-weight: 700;
  font-size: 3.8rem;
  line-height: 1.2;
  margin-bottom: 18px;
}
@media only screen and (min-width: 768px) {
  .hero-title {
    font-size: 5.8rem;
    margin-bottom: 24px;
  }
}

.hero-subtitle {
  font-size: 1.4rem;
  line-height: 1.5;
  margin-bottom: 32px;
}
@media only screen and (min-width: 768px) {
  .hero-subtitle {
    font-size: 1.6rem;
    margin-bottom: 48px;
  }
}

.imageText-container {
  display: flex;
  justify-content: space-between;
  gap: 32px;
  flex-direction: column-reverse;
  align-items: center;
}
@media only screen and (min-width: 768px) {
  .imageText-container {
    gap: 48px;
  }
  .imageText-container.image-right {
    flex-direction: row;
  }
  .imageText-container.image-left {
    flex-direction: row-reverse;
  }
  .imageText-container.image-top {
    align-items: flex-start;
  }
}

.imageText-text {
  flex: 1;
}
@media only screen and (min-width: 768px) {
  .imageText-text {
    flex: 1 1 544px;
  }
}
.imageText-text ul,
.imageText-text ol {
  padding-left: 32px;
  padding-top: 6px;
}
.imageText-text ul li,
.imageText-text ol li {
  margin-bottom: 8px;
}
.imageText-text ul li:last-of-type,
.imageText-text ol li:last-of-type {
  margin-bottom: 0;
}
.imageText-text h3 {
  margin-top: 16px;
  margin-top: 16px;
}

.imageText-image {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
}
@media only screen and (min-width: 768px) {
  .imageText-image {
    flex: 1 1 600px;
    max-width: 600px;
  }
}

.imageText-pretitle {
  margin-bottom: 12px;
  font-size: 1.8rem;
}

.header {
  padding-top: 25px;
  padding-bottom: 25px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 30;
}

.logo {
  max-width: 170px;
}
.logo svg {
  width: 100%;
  height: auto;
}
.logo svg path {
  fill: #231f20;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  width: 100%;
  gap: 24px;
}
@media only screen and (min-width: 768px) {
  .nav-container {
    flex-direction: row;
  }
}

.phone-menu {
  color: #333;
  font-weight: 600;
  transition: color 0.2s ease;
  font-size: 1.6rem;
}
@media only screen and (min-width: 768px) {
  .phone-menu {
    font-size: 2rem;
  }
}
.phone-menu:hover {
  color: #2ebcb3;
  text-decoration: none;
}

.nav-wrap span {
  font-style: italic;
  font-size: 1.4rem;
  margin-right: 6px;
}
@media only screen and (min-width: 768px) {
  .nav-wrap span {
    font-size: 1.6rem;
  }
}
.nav-wrap a {
  color: #333;
  font-size: 1.8rem;
  font-weight: 700;
}
@media only screen and (min-width: 768px) {
  .nav-wrap a {
    font-size: 2rem;
  }
}